@import '../../../variables.scss';

.label {
    margin-top: 25px;

    display: flex;
    flex-direction: column;
    font-size: 16px;
    color: $gray;

    select,
    input,
    textarea {
        margin-top: 15px;
        height: 43px;
        background: #FFFFFF;
        border: 1px solid #C7C7C7;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
        border-radius: 5px;
        font-size: 16px;
        padding-left: 10px;

    }

    textarea {
        padding-top: 8px;
        height: auto;
    }

    input[type="checkbox"] {
        width: 15px;
        height: 15px;
        margin-left: 5px;
        margin-bottom: 10px;
    }
}